import request from '@/utils/request4Quantization'
import config from '@/config'

// 
export function get_code_template_group_page_list(params, isLoading) {
  return request({
    url: config.kkautogenerator_api_url + '/kkautogenerator/api/v1/code_template_group/get_code_template_group_page_list',
    method: 'post',
    data: params,
    isLoading
  })
}
